@import url('https://fonts.googleapis.com/css?family=Roboto:300,500,700');

body{
  background-color: #f7e5d9;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: Roboto, sans-serif;
  font-weight: 300;
}

//Nav bar Styles
#main-nav{
  border-bottom: 1px solid #dfcfc4; 
}

#main-nav ul{
  margin: 0;
  padding: 0;

}

#main-nav ul li{
  list-style-type: none;
  display: inline-block;
  padding: 1.5em 0em 0.9em 2.3em;

}


#main-nav a {
  color: #131313;
  text-decoration: none;
  font-weight: 300;
  font-size: 1em;
}


#main-nav a:hover {
  font-weight: 500;

}

#main-nav a:active{
  color: red;
}

#main-nav a:focus{
  font-weight: 500;
}
// END NAV BAR


//WORK INDEX PAGE



#work-components-container{
  position: relative;
  //border: 1px solid green;
  height: 100%;
  top: 100px;
  white-space: nowrap;
}


.work-head h1{
  position: relative;
  //border: 1px solid green;
  margin: 0;
  line-height: 1.4em;
  font-weight: 500;
  font-size: 2.25em;
}

.work-component{
  position: relative;
  //border: 1px solid red;
  width: 400px;
  height: 480px;
  display: inline-block;
  white-space: normal;
  overflow: hidden;
  padding-left: 2.5em; 
}


.work-details{
  width: 390px;
  height: 120px;
  overflow: auto;
  border: 1px solid green;

}

.work-role{
  font-weight:500;
}


.work-dates{
  border: 1px solid green;
  width: 100px;
  display: inline-block;

}

.work-projects{
  display: inline-block;
  width: 100px;
}

.work-image{
  position: relative;
  width: 400px;
  height: 225px;
  overflow: hidden;
}


.work-image img {
  width:100%;
  vertical-align:top;
}

.work-image-overlay{
  position: absolute;
  top: -100%;
  left: -100%;
  width: 100%; 
  height: 100%;
  z-index: 1;
  background-color: #fb5353;
  text-align: center;
  opacity: 0.8;
  transition: all 0.2s;
  -webkit-transition: all 0.2s;
}

.work-image:hover .work-image-overlay{
  top: 0%;
  left: 0%;
}

.work-projects-link{
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  margin: 0 auto;
  font-weight: 500;
  font-size: 1.5em;
  color: white; 
  text-decoration: underline;
}

